.App {
  text-align: center;
  
}


body{
  background-color: rgb(56,56,56);
}




.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(255,255,255,0.2);
  transition: 0.3s;
  background-color:   rgb(224,255,255);
  height:400px;
  width:200px;
  float:left;
  margin-right:90px;
  margin-left: 90px;
  margin-top: 200px;
  color: rgb(0,0,0);
  border-radius: 10px;
  

}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(255,255,255,0.8);
}

/* Add some padding inside the card container */
.crypto-container {
  padding: 2px 16px;
}

.card p{
  font-size: 20px;
}
.image{
  height: 60px;
  width:50px;
}
.button {
   
    appearance: button;

    text-decoration:none;
    color: black;
}
.Header ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(224,255,255);
}

.Header li {
  float: left;
}

.Header li a {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
.Header li a:hover {
  background-color: cyan;
}

.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(224,255,255);
  color: black;
  text-align: center;
}

.Header h1{
  color:white;
  font-family: monospace;
  font-style: italic;
  font-weight:10px;
}
